import React from "react";
import { Group, Image, Sprite } from "react-konva";
import { gsap } from "gsap/all";

export default class ImageIcon extends React.PureComponent {
	constructor(props) {
		super(props);
		this.width = window.innerWidth * 0.5;
		this.state = {
			image: null,
			x: this.width - 26,
			y: 50,
		};
		// this.imgRef = React.createRef(); 
	}
	animate(){
		gsap.to(this.imgRef, {duration:0.4, repeat:5,scaleX:1.25,scaleY:1.25,opacity:1, onComplete:()=>{this.imgRef.attrs.scaleX = 1;this.imgRef.attrs.scaleY = 1}});

	}
	componentDidMount() {
		this.loadImage(this.props.image);
	}
	componentDidUpdate() {
		// if (this.cardStyle !== this.props.cardStyle) {
		// 	this.loadImage(this.props.cardStyle);
		// 	this.cardStyle = this.props.cardStyle;
		// 	// console.log("yes yes");
		// }
		// this.sound.play();
	}

	componentWillUnmount() {
		this.image.removeEventListener("load", this.handleLoad);
	}
	loadImage(img) {
		// save to "this" to remove "load" handler on unmount
		this.image = new window.Image();
		this.image.src = img;
		this.image.addEventListener("load", this.handleLoad);
	}
	handleLoad = () => {
		this.setState({
			image: this.image,
		});
	};

    // handleButtonClick(e){
    //     console.log("yes");
    //     console.log(e);
    // }
	render() {
		return (
			// <Group >
				<Image
					x={this.props.x}
					y={this.props.y}
					image={this.state.image}
                    // onClick={(e)=>{console.log("hello");}}
					// scaleY={0.95}
					ref={img =>this.imgRef = img}
				></Image>
			// </Group>
		);
	}
}
