import "../../../css/games/inside-out/index.css";
import "../../../css/ui/buttons.css";
import "../../../css/ui/gameBoard.css";
import { useNavigate, useLocation } from "react-router-dom";
import { useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../../../state/reducers";

import Table from "./table";


export default function InsideOut() {
    const state: any = useSelector((state: RootState) => state);
    const { userDetails, device, lastBets } = state;
    const history = useNavigate();
    const location = useLocation();
    return (
        <div className="fs">
            <div
                className="games-lobby-btn"
                onClick={(e) => {
                    e.preventDefault();
                    history("/casino");
                }}
            >
                Casino Lobby
            </div>
            <div className="layout portraight">
                <div className="insideOut_gameBoard">
                    <Table device={device}></Table>
                </div>
            </div>
        </div>
    );
}
