import React from "react";
import CardDefault from "../../../../assets/playingCards/cards.png";
// import CardSound from "../assets/Card.wav";
// import CardStyleOne from "../../../../../../assets/images/table/cards/cards2.png";
// import CardStyleTwo from "../../../../../../assets/images/table/cards/cards3.png";
import { Group, Image, Sprite } from "react-konva";
import { gsap } from "gsap/all";

export default class Card extends React.PureComponent {
	constructor(props) {
		super(props);
		this.width = window.innerWidth * 0.5;
		this.state = {
			image: null,
			x: this.width - 26,
			y: 50,
		};
		this.cardRef = React.createRef();
		this.cardStyle = CardDefault;

		// console.log(props);
		// this.sound = new Audio(CardSound);
		// this.sound.play();

		
	}

	componentDidMount() {
		this.loadImage(CardDefault);
		setTimeout(() => {
			this.setState({ x: this.props.x, y: this.props.y });
			// this.cardSound.play()
		}, 150);

		gsap.to(this.cardRef, {x:this.props.x + 80 ,y:this.props.y, duration:0.4, repeat:0,scaleX:1,scaleY:1,opacity:1, onComplete:()=>{console.log("animation complted")}});
		// console.log(this.cardRef);
	}
	componentDidUpdate() {
		// if (this.cardStyle !== this.props.cardStyle) {
		// 	this.loadImage(this.props.cardStyle);
		// 	this.cardStyle = this.props.cardStyle;
		// 	// console.log("yes yes");
		// }
		// this.sound.play();
	}

	componentWillUnmount() {
		this.image.removeEventListener("load", this.handleLoad);
	}
	loadImage(img) {
		// save to "this" to remove "load" handler on unmount
		this.image = new window.Image();
		this.image.src = img;
		this.image.addEventListener("load", this.handleLoad);
	}
	handleLoad = () => {
		this.setState({
			image: this.image,
		});
	};

	render() {
		// console.log("card updated");
		return (
			<Group>
				<Sprite
					x={window.innerHeight-75}
					y={20}
					image={this.state.image}
					scaleX={this.props.scale}
					scaleY={this.props.scale}
					animations={{
						cardFrame: this.props.frame
					}}
					animation={"cardFrame"}
					frameRate={1}
					frameIndex={0}
					opacity={this.props.alpha}
					visible={this.props.show}
					ref={img=>this.cardRef = img}
				></Sprite>
			</Group>
		);
	}
}
